<!-- MessageBox.vue -->
<template>
  <div class="msg-box">
    <div class="icon-container">
      <img :src="imagePath" alt="사용자 아이콘" />
    </div>
    <div class="msg-container">
      <div class="msg">
        <p :style="{ textAlign: isLoading ? 'center' : 'left' }">
          {{ content }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'messageBox',
  props: {
    content: {
      type: String,
      required: true,
    },
    isBot: {
      type: Boolean,
      required: true,
    },
    caseNums: {
      type: Array,
      default: () => {
        return [];
      },
    },
    isLoading: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  data() {
    return {
      userIconPath: require('@/assets/icons/user_icon_default.png'),
      botIconPath: require('@/assets/icons/kyobot_icon.png'),
      caseNumsMock: ['95다39533', '67누890'],
    };
  },
  computed: {
    imagePath() {
      return this.isBot ? this.botIconPath : this.userIconPath;
    },
  },
};
</script>

<style scoped>
.msg-box {
  display: flex;
  width: 97%;
  margin: 5% 0;
  align-items: flex-start;
}
.icon-container img {
  width: 30px;
  margin-top: 5px;
  padding: 2px;
  border: 1px solid rgb(104, 104, 104);
  border-radius: 50%;
  object-fit: cover;
}
.msg-container {
  max-width: 100%;
  margin-left: 10px;
  flex-grow: 1;
}
.loading-img {
  display: block;
  width: 100px;
  margin: 0 auto;
}
.msg {
  display: inline-block;
  border: 1px solid lightgray;
  border-radius: 5px;
  padding: 8px;
  white-space: pre-wrap;
  word-break: break-all;
  overflow-wrap: break-word;
}
.caseref-box {
  display: flex;
  flex-wrap: wrap;
  width: 97%;
}
.caseref-move,
.caseref-enter-active {
  transition: all 0.5s ease;
}
.caseref-enter-from {
  opacity: 0;
  transform: translateX(-10%);
}
.caseref-enter-to {
  opacity: 1;
}
</style>
