<!-- InfofWindow -->
<template>
  <section id="info-window">
    <header id="info-window-header">
      <h2>KYOBOT 안내 및 설명</h2>
      <button @click="$emit('closeWindow')" class="btn-close">X</button>
    </header>
    <article class="info-box">
      <h2>KYOBOT 설명</h2>
      <hr />
      <p>
        ● KYOBOT은 고객분류와 조건에 맞게 보험상품을 추천하고, 그에 알맞는
        마케팅문구를 생성해주는 챗봇입니다.
      </p>
      <p>
        ● KYOBOT은 현재 기능을 보여주는것을 목적으로 하고있으므로,
        성능적측면에선 협업결정이후 보강예정입니다.
      </p>
      <p>
        ● 유연한 모듈형 구조를 채택하고 있기때문에 교보생명측 요구에 맞춰
        데이터구조, 모듈구조, 작동순서등 변경 가능합니다.
      </p>

      <h2>KYOBOT 구조도</h2>
      <hr />
      <p>※자유롭게 확대 & 축소 & 이동해보세요.</p>
      <div class="panzoomContainer" ref="panzoomContainer">
        <img
          ref="panzoomDiagram"
          :src="KYOBOTDiagramPath"
          alt="KYOBOT 구조도"
        />
      </div>
      <p>
        ● RAG파트, 키워드추출 및 추천파트, 프롬프트파트, 에이전트 파트로
        구성되어있습니다.
      </p>
      <p>
        ● <b>RAG파트는</b> DB에서 보험정보와 마케팅문구예시를 검색하고 가져오는
        기능을 수행합니다.
      </p>
      <p>
        ● <b>키워드추출 및 추천파트</b>는 질문에서 키워드를 추출하고 그로부터
        상품을 추천해주는 기능을 수행합니다.
      </p>
      <p>
        ● <b>프롬프트 파트</b>는 키워드, 추천보험정보, 예시문구를 받아 특정한
        규칙대로 LLM에게 지시합니다.
      </p>
      <p>
        ● <b>에이전트파트</b>는 받은 지시를 바탕으로 스스로 판단하며 기능을
        수행하여 결과물을 사용자에게 보냅니다.
      </p>
      <p>
        ● 현재 임시페이지는 'RAG파트 일부'와 '에이전트파트'의 에이전트 기능을
        제외하고 개발하였습니다. (추후 추가 및 변경 가능합니다.)
      </p>
      <h2>질문 작성 및 답변 가이드</h2>
      <hr />
      <p>
        ● 현재 KYOBOT이 추출할 수 있는 키워드는 6종류이며, 전체 목록은 아래와
        같습니다.
      </p>
      <!-- prettier-ignore -->
      <p style="border: 1px solid gray; padding: 3px; border-radius: 5px">
        1. 성별 : 남자, 여자<br/>
        2. 연령대 : 10~90대 (질문에 없으면 -1)<br/>
        3. 질병이력 : 없음, 간암, 갑상선암, 고혈압, 관절염, 뇌질환, 당뇨, 위암, 피부병<br/>
        4. 음주유무/량 : 없음, 적음, 중간, 많음<br/>
        5. 흡연유무/량 : 없음, 적음, 중간, 많음<br/>
        6. 결혼여부 : 미혼, 기혼<br/>
      </p>
      <p>
        ● 질문을 입력하면 질문에서 상기 6개의 키워드를 추출하고 맞는 보험상품을
        선택합니다.
      </p>
      <p>
        ● 이후 <b>추출된 키워드</b>와 <b>보험상품</b>, 그리고
        <b>마케팅문구</b> 3가지가 포함된 답변을 KYOBOT이 생성합니다.
      </p>
    </article>
    <footer id="info-window-footer"></footer>
  </section>
</template>

<script>
// timmywil-panzoom 사용하여 KYOBOT 구조도 패닝 및 확대축소 가능하도록 설정
import Panzoom from '@panzoom/panzoom';

export default {
  data() {
    return {
      KYOBOTDiagramPath: require('@/assets/images/KYOBOT_diagram.png'),
      panzoomInstance: null,
    };
  },
  mounted() {
    this.panzoomInstance = Panzoom(this.$refs.panzoomDiagram, {
      maxScale: 4,
      minScale: 0.5,
      startScale: 0.5,
      contain: 'outside',
    });
    this.$refs.panzoomContainer.addEventListener('wheel', (event) => {
      this.panzoomInstance.zoomWithWheel(event);
    });
  },
  beforeUnmount() {
    this.$refs.panzoomContainer.addEventListener('wheel', (event) => {
      this.panzoomInstance.zoomWithWheel(event);
    });
  },
};
</script>

<style scoped>
#info-window {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 600px;
  box-shadow: lightgray 0 0 10px 0;
  background: white;
}
#info-window-header {
  width: 100%;
  height: 50px;
  flex-shrink: 0;
  text-align: center;
  position: relative;
  border-bottom: 1px solid lightgray;
}
#info-window-header h2 {
  color: #071f59;
  line-height: 50px;
  font-size: 25px;
  z-index: 1;
}
.info-box {
  width: 90%;
  margin-top: 5px;
  flex-grow: 1;
  padding: 5px 10px;
  overflow-y: scroll;
  line-height: 1.8rem;
}
.info-box h2 {
  color: #071f59;
  font-size: 1.2rem;
  margin-top: 30px;
}
.info-box p {
  margin-top: 5px;
  font-size: 0.9rem;
  white-space: pre-wrap;
}
.panzoomContainer {
  width: 99%;
  height: 300px;
  border: 1px solid gray;
  overflow: hidden;
}
.panzoomContainer img {
  max-width: 800px;
}
#info-window-footer {
  width: 100%;
  height: 20px;
  flex-shrink: 0;
}
.warning {
  margin: 0 auto;
  text-align: center;
}
.btn-close {
  border: 1px solid lightgray;
  border-radius: 5px;
  font-size: 20px;
  width: 30px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 3;
}
.btn-close:hover {
  background-color: lightgray;
}
.btn-close:active {
  background-color: gray;
}

@media screen and (max-width: 1024px) {
  #info-window {
    position: absolute;
    right: 0;
    top: 0;
    min-width: 320px;
    max-width: 768px;
    width: 80%;
    height: 100%;
    z-index: 3;
  }
}
</style>
