<template>
  <div id="root-div">
    <transition name="slide-nav" @after-leave="afterLeaveNav">
      <nav-bar
        v-show="navVisible"
        @update:selectedHistUUID="updateHistUUID"
        @closeNav="closeNav"
        :style="{ zIndex: zIndexNav }"
      />
    </transition>
    <section id="chat-window" :style="{ zIndex: zIndexChat }">
      <button @click="showInfo" class="btn-show-info">KYOBOT 설명</button>
      <header id="chat-window-header">
        <img
          id="hamburger"
          @click="showNav"
          :src="hamburgerBtnPath"
          alt="menu_button"
        />
        <img id="chat-header-logo" :src="LogoPath" alt="kyobot_logo" />
        <h2>KYOBOT</h2>
      </header>
      <article id="chat-box" ref="chatBox">
        <message-box
          v-for="(chat, index) in currentChatHist"
          :key="index"
          :isBot="chat.isBot"
          :content="chat.content"
          :isLoading="chat.isLoading ? true : false"
        />
        <div v-if="isBegin" id="first-readme">
          <p>KYOBOT기능의 예시페이지 입니다.</p>
          <p>※ 임시 모델이므로 성능외적 측면을 평가부탁드립니다.</p>
          <p>※ 설명은 오른쪽 상단의 버튼을 클릭하시면 됩니다.</p>
          <p>※ 아래 3개의 질문은 예시 질문입니다.</p>
        </div>
        <div v-if="isBegin" id="query-example">
          <p
            @click="
              sendMessage(
                '위암 이력이 있는 40대 남성을 위한 마케팅문구를 생성해줘.'
              )
            "
          >
            위암 이력이 있는 40대 남성을 위한 마케팅문구를 생성해줘.
          </p>
          <p
            @click="
              sendMessage(
                '음주 및 흡연 없고, 질병이력 없는 청년층을 위한 보험과 그에 맞는 광고문구를 만들어줄래?'
              )
            "
          >
            음주 및 흡연 없고, 질병이력 없는 청년층을 위한 보험과 그에 맞는
            광고문구를 만들어줄래?
          </p>
          <p
            @click="
              sendMessage(
                '72살 당뇨 고위험군인 여성을 위한 보험있어? 예시 마케팅문구도 알려줘.'
              )
            "
          >
            72살 당뇨 고위험군인 여성을 위한 보험있어? 예시 마케팅문구도 알려줘.
          </p>
        </div>
      </article>
      <div id="input-box">
        <textarea
          v-model="userInput"
          id="input-textarea"
          ref="input-textarea"
          rows="1"
          placeholder="질문을 입력하세요."
          :style="{ height: '${height}px' }"
          :readonly="isDisabled"
          @keydown.enter="handleEnter"
        ></textarea>
        <button
          id="input-send-button"
          :disabled="isDisabled"
          @click="isDisabled ? null : sendMessage()"
        >
          ↑
        </button>
      </div>
    </section>
    <transition name="slide-case-window" @after-leave="afterLeaveRef">
      <info-window
        v-show="infoWindowVisible"
        :style="{ zIndex: zIndexRef }"
        @closeWindow="closeInfo"
      />
    </transition>
    <transition name="overlay">
      <div
        id="overlay"
        v-show="overlayVisible"
        :style="{ zIndex: zIndexOverlay }"
      ></div>
    </transition>
  </div>
</template>

<script>
import MessageBox from '@/components/MessageBox.vue';
import NavBar from '@/components/NavBar.vue';
import InfoWindow from '@/components/InfoWindow.vue';

export default {
  components: { MessageBox, NavBar, InfoWindow },
  data() {
    return {
      hamburgerBtnPath: require('@/assets/icons/hamburger.png'),
      LogoPath: require('@/assets/icons/kyobot_icon.png'),
      baseURL: process.env.VUE_APP_API_BASE_URL,
      chatHistUUID: '', // NavBar에서 받아올 대화내역 UUID
      userInput: '', // 사용자 입력 메시지 양방향 바인딩

      isDisabled: false, // 봇답변시 입력창 및 버튼 비활성화 관리
      navVisible: false,
      overlayVisible: false,
      infoWindowVisible: false,
      isBegin: true,

      // z-index 관리. 모바일 & 태블릿환경에서 특정요소에 집중할때만 zIndex 오버레이보다 높게 설정
      zIndexNav: 1,
      zIndexChat: 1,
      zIndexInfo: 1,
      zIndexOverlay: 2,

      /** 채팅내역이 기록될 오브젝트
       * @type { uuid:[{content: string, isBot: boolean, caseNums: array[string]}, {content: ...}], uuid:[{...}, ...] }
       * @property {string} uuid -> 대화내역 uuid (a-zA-z0-9 3글자)
       * @property {string} content -> 채팅 메시지
       * @property {boolean} isBot -> 봇인지 사람인지 여부 (아이콘 구별)
       * @property {array[string]} caseNums -> 참고판례(사건번호) 목록. 봇인경우만 조건부로 입력
       * @property {key:array} temp:[] -> uuid가 없을시 임시 대화내역 저장공간 (4글자로 key 겹침 x)
       */
      allChats: { temp: [] },
    };
  },
  computed: {
    /** 반응형변수. 현재 uuid에 해당하는 대화내역이 있는지 확인하고 없으면 빈 어레이 반환 */
    currentChatHist() {
      return this.allChats[this.chatHistUUID] || [];
    },
  },
  watch: {
    userInput() {
      this.adjustHeight();
    },
  },
  mounted() {
    this.adjustVh();
    window.addEventListener('resize', this.adjustVh);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.adjustVh);
  },
  methods: {
    updateHistUUID(selectedHistUUID) {
      this.chatHistUUID = selectedHistUUID;
      // 변경된 uuid가 allchats key에 없으면 추가
      if (!(selectedHistUUID in this.allChats)) {
        this.allChats[selectedHistUUID] = [];
      }
    },
    handleEnter(event) {
      if (this.isDisabled) {
        return;
      }
      if (event.shiftKey) {
        return;
      } else {
        event.preventDefault();
        this.sendMessage();
      }
    },
    adjustHeight() {
      this.$nextTick(() => {
        const element = this.$refs['input-textarea'];
        element.style.height = 'auto';
        element.style.height = `${element.scrollHeight}px`;
      });
    },
    adjustVh() {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    },
    showInfo() {
      this.zIndexRef = 3;
      this.$nextTick(() => {
        this.infoWindowVisible = true;
        this.overlayVisible = true;
      });
    },
    closeInfo() {
      this.infoWindowVisible = false;
      this.overlayVisible = false;
    },
    showNav() {
      this.zIndexNav = 3;
      this.navVisible = true;
      this.overlayVisible = true;
    },
    closeNav() {
      this.navVisible = false;
      this.overlayVisible = false;
    },
    afterLeaveNav() {
      this.zIndexNav = 1;
    },
    afterLeaveRef() {
      this.zIndexRef = 1;
    },
    scrollBottom(refName) {
      this.$nextTick(() => {
        let ref = this.$refs[refName];
        if (ref) {
          ref.scrollTo({ top: ref.scrollHeight, behavior: 'smooth' });
        }
      });
    },
    async sendMessage(message) {
      let userInput;
      if (message) {
        userInput = message;
      } else {
        userInput = this.userInput;
      }
      if (!userInput.trim()) return;
      this.isBegin = false;

      let chatArray = this.allChats[this.chatHistUUID] || this.allChats['temp'];
      chatArray.push({ content: userInput, isBot: false });
      this.userInput = '';
      this.isDisabled = true;
      this.scrollBottom('chatBox');

      setTimeout(() => {
        chatArray.push({
          content: 'KYOBOT이 마케팅문구를 생성중입니다...',
          isBot: true,
          isLoading: true,
        });
        this.scrollBottom('chatBox');
      }, 500);

      try {
        let response = await fetch(`${this.baseURL}/api/v1/query`, {
          method: 'POST',
          headers: { 'content-type': 'application/json' },
          body: JSON.stringify({ query: userInput }),
        });

        if (!response.ok) {
          throw new Error(`error! status: ${response.status}`);
        }
        let responseData = await response.json();
        let lastChat = chatArray[chatArray.length - 1];
        lastChat.content = responseData.ans;
        delete lastChat.isLoading;
        this.scrollBottom('chatBox');
      } catch (error) {
        console.error('서버로부터 응답 받아오기 실패', error);
        chatArray.push({
          content: '오류가 발생했습니다. 다시 시도해주세요.',
          isBot: true,
        });
      }
      this.isDisabled = false;
    },
  },
};
</script>

<style>
